import { useEffect, useRef } from 'react'
import wallpaper from '../assets/wallpaper.jpg'
import Logo from '../components/Logo'
import GitHub from '../assets/GitHub_Logo.png'
import me from '../assets/me.jpg'
import { gsap } from "gsap";
import SplitText from 'gsap/SplitText';
import ScrollTrigger from "gsap/ScrollTrigger";
import SkillSection from '../components/Skill'

gsap.registerPlugin(SplitText, ScrollTrigger);

export default function Home() {
  const barsRef = useRef<(HTMLDivElement | null)[]>([]);
  
  useEffect(() => {
    const split = SplitText.create(".about", { type: "words"});
    gsap.from(split.words, {
      y: 100,
      autoAlpha: 0,
      stagger: 0.005,
      duration: 2,
      ease: "power3.out",
      scrollTrigger: {
        trigger: ".about",
        start: "top 80%",
        toggleActions: "play none none none",
        id: "aboutAnimation",
      },
    });

    return () => {
      split.revert();
    };
  }, []);

  const containerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const container = containerRef.current;
    if (!container) return;
  
    const totalScrollWidth = container.scrollWidth - container.clientWidth;
  

    const horizontalTween = gsap.to(container, {
      x: -totalScrollWidth,
      ease: "none",
    });
  
    ScrollTrigger.create({
      animation: horizontalTween,
      trigger: container,
      start: "top top",
      end: () => `+=${container.scrollWidth}`,
      scrub: true,
      pin: true,
      pinSpacing: true,
      anticipatePin: 1,
      id: "horizontalScroll",
    });

    barsRef.current.forEach((bar) => {
      if (bar) {
        gsap.set(bar, { width: "0%" });
  
        gsap.to(bar, {
          width: bar.dataset.width,
          duration: 0.5,
          ease: "power3.out",
          scrollTrigger: {
            trigger: bar,
            start: "left center",
            toggleActions: "play none none reverse",
            containerAnimation: horizontalTween,
          },
        });
      }
    });
  
    ScrollTrigger.refresh();
  
    return () => {
      ScrollTrigger.getAll().forEach(trigger => trigger.kill());
    };
  }, []);
  
  const skills = [
    { name: 'Python', years: 5, width: '83.33%', color: 'from-blue-500 to-blue-600' },
    { name: 'JavaScript', years: 4, width: '66.67%', color: 'from-yellow-500 to-yellow-600' },
    { name: 'TypeScript', years: 3, width: '50%', color: 'from-blue-400 to-blue-500'},
    { name: 'Java', years: 3.5, width: '58.33%', color: 'from-red-500 to-red-600' },
    { name: 'C++', years: 2, width: '33.33%', color: 'from-indigo-500 to-indigo-600'},
    { name: 'React', years: 2.5, width: '41.67%', color: 'from-cyan-500 to-cyan-600'},
    { name: 'SQL', years: 4.5, width: '75%', color: 'from-orange-500 to-orange-600'},
    { name: 'Go', years: 1.5, width: '25%', color: 'from-teal-500 to-teal-600'},
  ];

  const frameworks = [
    { name: 'React', years: 2.5, width: '41.67%', color: 'from-cyan-500 to-cyan-600'},
    { name: 'Express', years: 3, width: '50%', color: 'from-green-500 to-green-600'},
    { name: 'Django', years: 2, width: '33.33%', color: 'from-emerald-600 to-emerald-700'},
    { name: 'Flask', years: 2, width: '33.33%', color: 'from-gray-600 to-gray-700'},
    { name: 'Node.js', years: 3, width: '50%', color: 'from-lime-600 to-lime-700'},
    { name: 'TensorFlow', years: 1.5, width: '25%', color: 'from-orange-500 to-orange-600'},
    { name: 'PyTorch', years: 1.5, width: '25%', color: 'from-red-600 to-red-700'},
    { name: 'Tailwind', years: 2, width: '33.33%', color: 'from-sky-500 to-sky-600'}
  ];

  const tools = [
    { name: 'Git', years: 4, width: '66.67%', color: 'from-orange-600 to-orange-700'},
    { name: 'Docker', years: 2, width: '33.33%', color: 'from-blue-500 to-blue-600'},
    { name: 'AWS', years: 1.5, width: '25%', color: 'from-yellow-600 to-yellow-700'},
    { name: 'MongoDB', years: 2.5, width: '41.67%', color: 'from-green-600 to-green-700'},
    { name: 'PostgreSQL', years: 3, width: '50%', color: 'from-blue-600 to-blue-700'},
    { name: 'Linux', years: 4, width: '66.67%', color: 'from-yellow-500 to-yellow-600'},
    { name: 'VS Code', years: 5, width: '83.33%', color: 'from-blue-400 to-blue-500'},
    { name: 'Postman', years: 2, width: '33.33%', color: 'from-orange-500 to-orange-600'}
  ];


  return (
    <>
      <div 
        className='relative h-screen bg-cover bg-center bg-fixed'
        style={{ backgroundImage: `url(${wallpaper})` }}
      >
        <h1 className='absolute top-[50%] left-[40%] -translate-x-1/2 -translate-y-1/2 font-poppins text-white text-8xl font-bold mb-3'>Ryan Zappone</h1>
        <a 
          className='absolute top-[55.5%] left-[31%] -translate-x-1/2 -translate-y-1/2'
          href='https://www.linkedin.com/in/ryan-zappone/' 
          target='_blank'
          rel="noreferrer"
        >
          <Logo/>
        </a>
        <a 
          className='absolute top-[55.5%] left-[39%] -translate-x-1/2 -translate-y-1/2'
          href='https://github.com/ryanzap48/' 
          target='_blank'
          rel="noreferrer"
        >
          <img src={GitHub} alt='github' className='w-28 h-auto'/>
        </a>
      </div>
      <div
        className='h-screen z-0 bg-white relative flex items-center justify-center gap-[7%]'
      >
        <img
          className='w-1/3 h-auto rounded-2xl'
          src={me}
          alt='about me'
        />
        <div className='w-1/3 max-h-screen'>
          <h1 className='text-2xl font-semibold text-gray-700 mb-3 font-poppins'>About Me</h1>
          <p className='about text-gray-600 font-poppins leading-[1.85]'>
            I am a third year BS/MS student pursuing a Bachelors in Computer Science, a Masters in Artificial Intelligence 
            as well as a Minor in Data Science at Worcester Polytechnic Institute. I took my first computer science class 
            my senior year of high school and immediately fell in love. Ever since then my drive for learning has been ever 
            long. The first ever programming language I learning was Java, which later led me to start assisting students in 
            our Java Object Oriented Design class my second year here. I learned Python, Assemble, C, and Rust during my 
            freshman year and immediately got hooked on grinding Project Euler as Leet Code problems. Fast forward a couple 
            years and many projects later, a new field started to catch my eye: Artificial Intelligence. As of late, I have 
            been especially interested in the fields of machine learning, specifically within the scope of optimization in 
            large databases as well as research in responsible uses, namely robustness against adversarial attacks. I as well 
            have a passion for full stack software engineering, with both front and back end development, and have gotten 
            especially advanced at both React and Express frameworks as well as Django and Flask.
          </p>
        </div>
      </div>
      <div className='wrapper overflow-x-auto'>
        <div ref={containerRef} className="flex gap-6 h-screen flex-nowrap">
          <div className='flex-shrink-0 min-w-[1000px] h-screen flex flex-col items-center justify-center px-20'>
            <SkillSection 
              title="Programming Languages"
              label="Languages"
              skills={skills}
              barsRef={barsRef}
              startIndex={0}
            />
          </div>
          <div className='flex-shrink-0 min-w-[1000px] h-screen flex flex-col items-center justify-center px-20'>
            <SkillSection 
              title="Frameworks & Libraries"
              label="Frameworks"
              skills={frameworks}
              barsRef={barsRef}
              startIndex={skills.length}
            />
          </div>
          <div className='flex-shrink-0 min-w-[1000px] h-screen flex flex-col items-center justify-center px-20'>
            <SkillSection 
              title="Tools & Technologies"
              label="Tools"
              skills={tools}
              barsRef={barsRef}
              startIndex={skills.length + frameworks.length}
            />
          </div>
        </div>
      </div>
      <div className='bg-white h-screen'>HLLEOEOE</div>
    </>
  );
}